

/* Responsive adjustments */
@media (max-width: 768px) {
    .restaurant-card {
        margin-bottom: 20px;
    }

    .title {
        font-size: 1.5em;
    }

    .MuiGrid-item {
        padding: 8px !important;
    }

    .MuiCardMedia-root {
        height: 100px;
    }

    .MuiTypography-h5 {
        font-size: 1.2em;
    }
}

@media (max-width: 480px) {
    .restaurant-card {
        margin-bottom: 15px;
    }

    .title {
        font-size: 1.2em;
    }

    .MuiCardMedia-root {
        height: 80px;
    }

    .MuiTypography-h5 {
        font-size: 1em;
    }
}
